      </el-row>
      <p>IDuserIDID</p>
      <el-row :gutter="20">
        <el-col :span="24"><div class="grid-content" @click="join">/</div></el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import compNav from '@/components/comp-nav.vue';
import roomLink from  '@/components/mixins/room-link';
import { clearUrlParam, genRoomId } from '@/utils/utils';

export default {
  name: 'App',
  mixins: [roomLink],
  components: {
    compNav,
  },
  data() {
    return {};
  },
  computed: {
    ...mapState({
      sdkAppId: state => state.sdkAppId,
      secretKey: state => state.secretKey,
      userId: state => state.userId,
      roomId: state => state.roomId,
      cameraId: state => state.cameraId,
      microphoneId: state => state.microphoneId,
    }),
  },
  methods: {
    handleValueChange(value, key) {
      this[key] = value;
    },
    join() {
      this.$router.push('/join');
    },
    fastJoin() {
      const link = this.generateRoomLink({
        roomId: genRoomId(),
        userId: this.userId,
      }, 'room');
      window.location.href = link;
    },
  },
  mounted() {
    clearUrlParam();
  },
};
</script>

